.view-container {
	width: 100%;
	height: 100%;
	min-height: 300px;
	position: relative;
	display: flex;
	flex-direction: column;
}

.content-container {
	width: 100%;
	overflow: auto;
	border-radius: 4px;
	position: relative;
	display: flex;
	justify-content: center;
}

.content-container::-webkit-scrollbar {
	width: 8px;
	height: 8px;
}

.content-container::-webkit-scrollbar-track {
	background: #f1f1f1;
	border-radius: 4px;
}

.content-container::-webkit-scrollbar-thumb {
	background: #888;
	border-radius: 4px;
}

.content-container::-webkit-scrollbar-thumb:hover {
	background: #555;
}

.graphviz-container {
	min-height: 300px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.graphviz-container > div {
	width: 100%;
	height: 100%;
	overflow: auto;
}

.html-content {
	padding: 10px;
}

.error-message {
	color: #d32f2f;
	padding: 16px;
	background-color: #ffebee;
	border-radius: 4px;
	margin: 10px 0;
}

.graph {
	width: 100%;
	height: 100%;
	min-height: 300px;
	max-height: 600px;
	overflow: hidden;
}

.MuiBox-root [role="dialog"] .MuiBox-root {
	max-height: 80vh;
	overflow-y: auto;
}

.content-container img {
	max-width: 100%;
	height: auto;
	display: block;
	margin: 0 auto;
}

.content-container pre {
	margin: 0;
	padding: 10px;
	white-space: pre-wrap;
	word-break: break-word;
	max-height: 600px;
	overflow: auto;
}

.content-container .chrome-picker {
	width: 100%;
	box-shadow: none;
}
